@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-style: $style;
    font-weight: #{$weight};
    font-family: "#{$font-family}";

    font-display: swap;
    src: url("../fonts/#{$url}.woff2") format("woff2"),
    url("../fonts/#{$url}.woff") format("woff");
  }
}

@include font-face("Roboto", "Roboto-Regular", 400, normal);
@include font-face("Roboto Condensed", "RobotoCondensed-Light", 300, normal);
@include font-face("Roboto Condensed", "RobotoCondensed-Regular", 400, normal);
@include font-face("Roboto Condensed", "RobotoCondensed-Bold", 700, normal);

@include font-face("Comforter", "Comforter-Regular", 400, normal);


// 100  Thin || Hairline
// 200  Extra Light || Ultra Light || Thin
